import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { Input } from '../../../../common/Textarea/formChilds';
import { requiredField } from '../../../../utils/validator';
import { ContactsEdit } from '../Contacts/contacts';


let ProfileDataForm = ({s, handleSubmit, profile}) => {
    return (
        <form onSubmit={handleSubmit} className={s.profileFooter}>
            <div className={s.profileDescr}><div>Рабочая информация</div></div>
            <div className={s.profileItem}><div className={s.aboutItem}>Полное имя:</div><div className={s.aboutDescr}><Field component={Input} placeholder="Полное имя" name="fullName" validate={[requiredField]} /></div></div>
            <div className={s.profileItem}><div className={s.aboutItem}>Обо мне:</div><div className={s.aboutDescr}><Field component={Input} placeholder="Обо мне" name="aboutMe" validate={[requiredField]} /></div></div>
            <div className={s.profileItem}><div className={s.aboutItem}>Ищу работу:</div><div className={s.aboutDescr}><Field component={Input} name="lookingForAJob" type="checkbox" validate={[requiredField]} /></div></div>
            <div className={s.profileItem}><div className={s.aboutItem}>Мои навыки:</div><div className={s.aboutDescr}><Field component={Input} placeholder="Опишите работу" name="lookingForAJobDescription" validate={[requiredField]} /></div></div>
            <div className={s.profileDescr}>Контакты</div>
            {Object.keys(profile.contacts).map( key => { return <ContactsEdit key={key} contactTitle={key} placeholder={profile.contacts[key]} name={'contacts.' + key} s={s} /> })}
            <button type="submit" >Сохранить</button>
        </form>
    )
}
ProfileDataForm = reduxForm({
    form: 'profileData'
})(ProfileDataForm)
export default ProfileDataForm;