import React, { useState } from 'react';
import { reduxForm, Field } from 'redux-form';
import pStyle from './profileInfo.module.scss';
import ProfileStatusWithHooks from './ProfileStatus/profileStatusWithHooks';
import {maxLengthCreator, requiredField} from '../../../utils/validator';
import { TextArea } from '../../../common/Textarea/formChilds';
import {Contacts} from './Contacts/contacts';
import ProfileDataForm from './profileDataForm/profileDataForm';

const maxLength = maxLengthCreator(100);
let PostsForm = ({ handleSubmit }) => {
    return (
        <form onSubmit={handleSubmit} className={pStyle.profileFooter__postwrite}>
            <Field
                name="postInput"
                component={TextArea}
                type="text"
                placeholder="Чем хотите поделиться?"
                validate={[requiredField, maxLength]}
            />
            <button type="sumbit" >Опубликовать</button>
        </form>
        
    )
}
PostsForm = reduxForm({
    form: 'post-form'
})(PostsForm)
const ProfileInfo = ({status, statusEditMode, toggleEditModeStatus, newStatusText, updateStatus, addPost, profile, isOwner, sendPhoto, updateProfileData, userId}) => {

    let [editMode, changeEditMode] = useState(false);

    let addNewPost = (values) => {
        addPost(values.postInput);
    }

    const onGetNewFile = (e) => {
        if (e.target.files.length) {
            sendPhoto(e.target.files[0])
        }
    }

    const onSubmit = async (formData) => {
        await updateProfileData(formData)
        changeEditMode(false)
    } 
    
    return (
        <div className={pStyle.profileInfo}>
            <div className={pStyle.avatar}>
                <img src={profile.photos.large ? profile.photos.large : 'https://html5css.ru/w3images/avatar2.png'} alt="myAvatar" />
                {isOwner && <input type={'file'} onChange={onGetNewFile} />}
            </div>
            <div className={pStyle.profileInner}>
                <div className="profileHeader">
                    <div className={pStyle.profileHeader__name}>
                        {profile.fullName}
                    </div>
                    <div className={pStyle.profileHeader__status}>
                        <ProfileStatusWithHooks status={status} editMode={statusEditMode} toggleEditMode={toggleEditModeStatus} newStatusText={newStatusText} updateStatus={updateStatus}/>
                    </div>
                    {
                        editMode ? 
                            <ProfileDataForm initialValues={profile} s={pStyle} onSubmit={onSubmit} profile={profile} /> 
                                : 
                            <ProfileData goToEditMode={() => changeEditMode(true)} isOwner={isOwner} profile={profile} />
                    }
                </div>
                <PostsForm onSubmit={addNewPost} />
            </div>
        </div>
    )
}
const ProfileData = ({profile, isOwner, goToEditMode}) => {
    return (
        <div className={pStyle.profileFooter}>
            <div className={pStyle.profileDescr}><div>Рабочая информация</div>{isOwner ? <div className={pStyle.editButton} onClick={goToEditMode}>Редактировать</div> : null}</div>
            <div className={pStyle.profileItem}><div className={pStyle.aboutItem}>Полное имя: </div><div className={pStyle.aboutDescr}>{profile.fullName ? profile.fullName : 'name'}</div></div>
            <div className={pStyle.profileItem}><div className={pStyle.aboutItem}>Обо мне: </div><div className={pStyle.aboutDescr}>{profile.aboutMe == null ? 'Пусто - 1' : profile.aboutMe}</div></div>
            <div className={pStyle.profileItem}><div className={pStyle.aboutItem}>Ищу работу:</div><div className={pStyle.aboutDescr}>{profile.lookingForAJob === false ? 'Не ищу' : 'Ищу'}</div></div>
            <div className={pStyle.profileItem}><div className={pStyle.aboutItem}>Мои навыки:</div><div className={pStyle.aboutDescr}>{profile.lookingForAJobDescription == null ? 'Пусто - 3' : profile.lookingForAJobDescription}</div></div>
            <div className={pStyle.profileDescr}>Контакты</div>
            {Object.keys(profile.contacts).map( key => { return <Contacts key={key} contactTitle={key} contactValue={profile.contacts[key]} s={pStyle} />})}
        </div>
    )
}

export default ProfileInfo;