import React from 'react';
import Post from './Post';
import p from './posts.module.scss';
const Posts = React.memo(({ posts, profile }) => {
    let { fullName, photos } = profile;
    let newPosts = posts.map(p => <Post key={p.id} id={p.id} message={p.message} name={fullName} avatar={photos.small} likes={p.likes} />)
    return (
        <div className={p.posts}>
            {newPosts}
        </div>
    )
})
export default Posts;