import React, { useEffect, useState } from 'react';
const ProfileStatusWithHooks = (props) =>  {
    /* state = {
        status: '',
        editMode: false
    }
    toggleEditMode = () => {
        this.setState({...this.state, editMode: !this.state.editMode});
    }
    newStatusText = (text) => {
        this.setState({...this.state, status: text});
    } */
    /* const { status, editMode, toggleEditMode, newStatusText,updateStatus} = props; */
    const {updateStatus} = props;

    /* let statusBar = React.createRef();
    const getText = () => {
        let text = statusBar.current.value;
        newStatusText(text);
    }
    const newStatus = () => {
        let text = statusBar.current.value;
        toggleEditMode(false)
        updateStatus(text);
    } */
    let [editMode, changeEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(()=>{
        setStatus(props.status);
    }, [props.status])

    /* debugger */
    const toggleEditMode = (boolean) => {
        changeEditMode(boolean);
    }

    const getText = (e) => {
        setStatus(e.currentTarget.value)
        /* let text = statusBar.current.value; */
        /* newStatusText(text); */
    }

    const newStatus = () => {
        /* let text = statusBar.current.value; */
        toggleEditMode(false)
        updateStatus(status);
    }

    return (
        <div className="status">
            { editMode ? 
                <div>
                    <input autoFocus onBlur={newStatus} value={status} onChange={getText}></input>
                </div>
                : 
                <div onClick={()=> toggleEditMode(true)}>
                    <span>
                        {status ? status : 'Установить статус'}
                    </span>
                </div> 
            }
        </div>
    )
}

export default ProfileStatusWithHooks;