import { Field } from "redux-form"
import { Input } from "../../../../common/Textarea/formChilds"
import { requiredField } from "../../../../utils/validator"

export const Contacts = ({contactTitle, contactValue, s}) => {
    return (
        <div className={s.profileItem}><div className={s.aboutItem}>{contactTitle}</div><div className={s.aboutDescr}>{contactValue == null ? 'Пусто' : contactValue}</div></div>
    )
}
export const ContactsEdit = ({contactTitle, placeholder, name, s}) => {
    return (
        <div className={s.profileItem}><div className={s.aboutItem}>{contactTitle}</div><Field component={Input} placeholder={placeholder ? placeholder : 'Ссылка'} name={name} validate={[requiredField]} /></div>
    )
}